<template>
	<div class="app-container test-PDF" v-loading="loading">
		<h2>PDF測試頁</h2>
		<div class="filter-container">
			<el-button
				class="filter-item"
				type="info"
				icon="el-icon-document"
				@click="handlePreview"
			>預覽PDF</el-button>
			<el-button
				class="filter-item"
				type="info"
				icon="el-icon-document"
				@click="handleDownload"
			>輸出PDF</el-button>
			<!-- <el-button class="filter-item" type="info" @click="setPDFinputs">更新內容</el-button> -->
		</div>

		<!-- <div id="viewContainer" />
		<div id="formContainer" /> -->

		<el-row :gutter="24">
			<el-col :span="12"><div ref="viewContainer" /></el-col>
			<el-col :span="12"><div ref="formContainer" /></el-col>
		</el-row>
	</div>
</template>

<script>
import { generate, BLANK_PDF } from '@pdfme/generator';
import { Form, Viewer } from '@pdfme/ui';
// import { getCaseCount } from "@/api/PI";

export default {
	name: "testPDF",
	components: { },
	data() {
		return {
			loading: false,
			timeTabId: 1,
		};
	},
	computed: { },
	watch: {},
	async created() {	
		// this.template = {};
		// this.form = {};
	},
	mounted() {
		this.initPDF();
	},
	methods: {
		initPDF() {
			const viewContainer = this.$refs.viewContainer;
			const formContainer = this.$refs.formContainer;
			// const viewContainer = document.getElementById('viewContainer');
			// const formContainer = document.getElementById('formContainer');

			const template = {
				basePdf: BLANK_PDF,
				schemas: [
					{
						a: {
							type: 'text',
							position: { x: 0, y: 0 },
							width: 10,
							height: 10,
						},
						b: {
							type: 'text',
							position: { x: 10, y: 10 },
							width: 10,
							height: 10,
						},
						c: {
							type: 'text',
							position: { x: 20, y: 20 },
							width: 10,
							height: 10,
						},
					},
				],
			};

			const inputs = [{ a: 'a1', b: 'b1', c: 'c1' }];

			this.Viewer = new Viewer({ domContainer: viewContainer, template, inputs });

			this.form = new Form({ domContainer: formContainer, template, inputs });
			this.form.onChangeInput(arg => console.log(arg));
		},
		handlePreview() {
			generate({ template: this.form.getTemplate(), inputs: this.form.getInputs(), options: { font: this.form.getFont() } }).then((pdf) => {
				// console.log(pdf);
				const blob = new Blob([pdf.buffer], { type: 'application/pdf' });
				window.open(URL.createObjectURL(blob));
			});
		},
		handleDownload() {
			generate({ template: this.form.getTemplate(), inputs: this.form.getInputs(), options: { font: this.form.getFont() } }).then((pdf) => {
				// console.log(pdf);
				const blob = new Blob([pdf.buffer], { type: 'application/pdf' });
				// window.open(URL.createObjectURL(blob));

				const filename = "成效式契約指標檢核表(附件)PI-3-1.pdf"; 
				const file = new File([blob], filename, { type: 'application/pdf' });
				const link = document.createElement('a');
				const url = URL.createObjectURL(file);
				link.href = url;
				link.download = file.name;
				document.body.appendChild(link);
				link.click();
				document.body.removeChild(link);
				URL.revokeObjectURL(url);
			});
		}
	},
};
</script>